.Statistics-box{
    width: 100%;
    height: 130vh;
    display: flex;
    flex-direction: column;
    align-items:center ;
    justify-content: center;
    background-color: rgb(223, 223, 223);
}

.Statistics-box .content-box{
    width: 45%;
    height: 50%;
    text-align: center;
    margin-top: 150px;
}
.Statistics-box .content-box h1 {
    font-size: 40px;
    font-weight: 700;
}

.Statistics-box .content-box p{
    font-size: 23px;
    font-weight: 500;
    color: var(--Grayish_Violet);
    line-height: 25px;
    margin-top: 20px;
}

.boxs{
    width: 100%;
    height: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

}
.boxs .all-box{
    background-color: white;
    width: 35%;
    height: 250px;
    margin-left: 40px;
    padding: 0 20px;
    z-index: 555;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: baseline;
    justify-content: space-between;
}
.boxs .all-box .icon{
    width: 90px;
    height: 90px;
    background-color: var(--Very_Dark_Blue);
    border-radius: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
}
.boxs .all-box .icon img{
    width: 50%;
    height: 50%;
    margin-top: auto;
    margin-bottom: auto;
}
.boxs .all-box h2{
    font-size: 24px;
    color: var(--Very_Dark_Violet);
}
.boxs .all-box p{
    font-size: 14px;
    color: var(--Grayish_Violet);
    font-weight: 500;
}
.boxs::after{
    content: '';
    width: 70%;
    height: 10px;
    background-color: var(--Cyan);
    z-index: 1;
    position: absolute;
    left: 10%;
    bottom: 70%;

}
.boxs :nth-child(1){
    transform: translateY(-40%);
}
.boxs :nth-child(2){
    transform: translateY(-20%);
}



@media (max-width:375px) {
    .Statistics-box{
        width: 100%;
        height: 200vh;
    }
    .Statistics-box .content-box{
        width: 95%;
        height: 50%;
        text-align: center;
        margin-top: 150px;
    }
    .Statistics-box .content-box h1 {
        font-size: 30px;
        font-weight: 700;
    }
    
    .Statistics-box .content-box p{
        font-size: 18px;
        font-weight: 500;
        color: var(--Grayish_Violet);
        line-height: 25px;
        margin-top: 20px;
    }
    .boxs{
        width: 100%;
        height: 60%;
        flex-direction: column;
    }
    .boxs .all-box{
        background-color: white;
        width: 100%;
        height: 250px;
        margin-left: 40px;
        padding: 0 20px;
        z-index: 555;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
    }
 
    .boxs .all-box{
        width: 100%;
    }
}